.container {
    position: absolute;
    right: 15px;
    top: 15px;
  
    font-size: 14px;
  
    display: flex;
    align-items: center;
  }
  .email {
    margin-right: 20px;
    letter-spacing: 1px;
  }
  .btn {
    border: 1px solidrgba(47, 139, 245, 0.952);
    color: #282828;
    background-color: transparent;
    padding: 8px 15px;
    font-weight: 600;
    border-radius: 10px;
    font-size: 14px;
    margin-right: 15px;
    box-shadow: 0px 5px 10px -8px black;
    transition: all 250ms linear;
  }
  .btn:hover,
  .btn:focus {
    background-color: rgba(47, 139, 245, 0.952);
    color: white;
  }
  .img {
    transition: all 250ms linear;
    border-radius: 50%;
    background-color: rgba(47, 139, 245, 0.952);
  }
  .img:hover {
    transform: scale(1.2);
  }
  .link {
    display: flex;
  }